.background {
    width: 100%;
    height:100%;
    top:0;
    left:0;
    background: url('../images/bullets.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 0;
    position: fixed;
}

.modal {
    display: block;
    background: #fff;
    width: 80%;
    height: 50%;
    top: 10%;
    left: 10%;
    padding: 20px;
    border: 1px solid #ccc;
}

@media screen and (max-width: 767px) {
    #page-wrapper {
        margin-top: 282px;
    }
}